import React from 'react';


const styles = {
	container : {
		overflowY   : 'hidden',
		overflowX   : 'auto',
		whiteSpace  : 'nowrap',
		padding     : '0px 10px',
		fontSize    : '0.5rem'
	}
};


export default ({ children, ... props }) => <div style={ styles.container } { ... props }>
	{ children }
</div>



