import React from 'react'

import HorzView from '../lib/HorzView'

import FONTS from '../../data/FONTS'


export default ({ font, onChange }) => <HorzView className="control">
	<div style={{ marginTop : '-20px', marginBottom : '20px' }}>
		{ FONTS.map ( f => <Font
			name={ f.name }
			title={ f.title }
			isSelected={ f.name === font }
			onChange={ onChange }
			key={ f.name }
		/> )}
	</div>
</HorzView>


const Font = ({ name, title, isSelected, onChange }) => <div
	onClick={ () => onChange ( name ) }
	className="font transition-all"
	style={{
		fontFamily : name,
		fontSize : '0.4rem',
		cursor  : 'pointer',
		display : 'inline-block',
		border  : 'none',
		padding : '10px 15px',
		paddintTop : '0px',
		borderBottom : '5px solid',
		borderBottomColor : isSelected
			? 'rgba( 166, 178, 192, 1 )'
			: 'rgba( 166, 178, 192, 0 )'
		,
		borderRadius : '10px',
		transitionDuration : '200ms'
	}}
>
	{ title }
</div>



