import React, { Component } from 'react'
import './editor.css'
import { Route } from 'react-router-dom'

import Result from './Result'
import Controls from './Controls'
import Wizard from '../Wizard'


const getVPWidth  = () => Math.max ( document.documentElement.clientWidth , window.innerWidth  || 0 )
const getVPHeight = () => Math.max ( document.documentElement.clientHeight, window.innerHeight || 0 )
const pxToResultSize = px => px * 38 / 100 - 30


export default class Editor extends Component {

	state = {
		// Image
		pack : 1,
		packRoof : 70,
		face : 1,
		imageSize : 35,

		// Text
		text : ' ',
		fontFamily : 'Dastnevis',
		color : '#2c3e50',
		fontSize : 75,

		// Background
		backgroundColor : '#ffffff',

		// ScreenSize
		screenSize : {
			width  : getVPWidth  (),
			height : getVPHeight ()
		}
	}

	setSize = () => this.setState ({ screenSize : {
		width  : getVPWidth  (),
		height : getVPHeight ()
	}})

	componentDidMount () {
		window.addEventListener ( 'resize', this.setSize, false )
	}

	componentWillUnmount () {
		window.removeEventListener ( 'resize', this.setSize )
	}

	render = () => <div className="editor">
		<Result
			text={ this.state.text }
			pack={ this.state.pack }
			face={ this.state.face }
			backgroundColor={ this.state.backgroundColor }
			imageStyle={{
				width  : this.state.imageSize + '%',
				height : this.state.imageSize + '%'
			}}
			textStyle={{
				fontFamily : this.state.fontFamily,
				fontSize : this.state.fontSize + 'px',
				color : this.state.color
			}}
			size={ this.calcSize () }
		/>

		<Controls
			{ ... this.state }
			onChange={ this.onChange }
		/>

		<Route path="/wizard" render={ props =>
			<Wizard
				{ ... props }
				onDone={ state => this.setState ( state ) }
			/>
		} />
	</div>

	onChange = ( stateName, newVal ) =>
		this.setState ({ [ stateName ] : newVal })

	calcSize () {
		const { width, height } = this.state.screenSize
		return 9 * width >= 13 * height
			? pxToResultSize ( width  ) // Landscape
			: pxToResultSize ( height ) // Portrait
	}

}



