import React from 'react'

import ColorBox from '../lib/ColorBox'
import EmojiBox from '../lib/EmojiBox'
import FontBox  from '../lib/FontBox'
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css'


export default ({
	onChange,
	backgroundColor,
	color,
	fontSize,
	imageSize,
	text,
	face,
	pack,
	packRoof,
	fontFamily
}) => <div className="controls-wrapper spacer">
	<div className="controls">
		
		{ /* Emoji pack */ }
		{ /* Emoji face */ }
		<label></label>
		<EmojiBox
			face={ face }
			pack={ pack }
			packRoof={ packRoof }
			onChange={ face => onChange ( 'face', face ) }
		/>

		{ /* Text */ }
		<label></label>
		<input
			className="control"
			type="text"
			value={ text }
			onChange={ e => onChange ( 'text', e.target.value ) }
		/>

		{ /* Backgorund color */ }
		<label>  </label>
		<ColorBox
			color={ backgroundColor }
			onChange={ color => onChange ( 'backgroundColor', color ) }
		/>

		{ /* Text color */ }
		{ text && <label> </label> }
		{ text && <ColorBox
			color={ color }
			onChange={ color => onChange ( 'color', color ) }
		/> }

		{ /* Font family */ }
		{ text && <label></label> }
		{ text && <FontBox
			font={ fontFamily }
			onChange={ font => onChange ( 'fontFamily', font ) }
		/> }

		{ /* Font size */ }
		{ text && <label> </label> }
		{ text && <div className="control ltr slider">
			<Slider
				min={ 30  }
				max={ 200 }
				step={ 0.01 }
				value={ fontSize }
				onChange={ value => onChange ( 'fontSize', value ) }
				tooltip={ false }
			/>
		</div> }
		
		{ /* Emoji size */ }
		{ face !== 0 && <label> </label> }
		{ face !== 0 && <div className="control ltr slider">
			<Slider
				min={ 10 }
				max={ 80 }
				value={ imageSize }
				onChange={ value => onChange ( 'imageSize', value ) }
				tooltip={ false }
			/>
		</div> }

		{ /* Bottom spacer */ }
		<div style={{ height : '50px' }}></div>

	</div>
</div>


