import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import './wizard.css'
import First from './First'
import Faz   from './Faz'


export default class Wizard extends Component {

    render = () => <div className="wizard-wrapper" onClick={ this.handleDone }>
        <div className="wizard" onClick={ e => e.stopPropagation () }>
            <Route exact path={ this.props.match.path } component={ First } />
            <Route path={ this.props.match.path + '/faz' } component={ Faz } />
        </div>
    </div>

    handleDone = () => {
        typeof this.props.onDone === "function"
            && this.props.onDone ({ hello : 'world' })
        this.props.history.push ( '/' )
    }

}



