import React from 'react'

import HorzView from '../lib/HorzView'

import COLORS from '../../data/COLORS'


export default ({ color, onChange }) => <HorzView className="control">
	<div style={{ marginBottom : '20px' }}>
		{ COLORS.map ( c =>
			<Color
				key={ c }
				color={ c }
				isSelected={ c === color }
				onChange={ onChange }
			/>
		) }
	</div>
</HorzView>


const Color = ({ color, isSelected, onChange }) => <div
	className="color transition-all"
	style={{
		backgroundColor : color,
		cursor  : 'pointer',
		display : 'inline-block',
		margin  : '0px 5px',
		borderRadius : isSelected ? '100%' : '5px',
		width : '7vmin', height : '7vmin'
	}}
	onClick={ () => onChange ( color ) }
></div>




