import React from 'react'
import { Link } from 'react-router-dom'


const styles = {
    addButton : {
        width : '200px', height : '200px',
        borderRadius : '100%',
        cursor : 'pointer',
        fontSize : '65px'
    }
}


export default ({ match }) => <div className="full-wh flex flex-center">
    <Link to={ match.path + '/faz' }><button className="button" style={ styles.addButton }>
        +
    </button></Link>
</div>



