import React from 'react'

import HorzView from '../lib/HorzView'


const styles = {
	emoji : ( isSelected, hasPadding ) => ({
		cursor  : 'pointer',
		display : 'inline-block',
		margin  : '0px 5px',
		borderRadius : '5px',
		width : '7vmin', height : '7vmin',
		background : isSelected ? 'rgba( 255, 255, 255, 1 )' : 'rgba( 255, 255, 255, 0 )',
		padding : hasPadding ? '5px' : '0px'
	})
}


export default ({ pack, packRoof, face, onChange }) => <HorzView className="control">
	<div>
		{ ( () => {
			var emojies = [];
			for ( let i = 0; i <= packRoof; i ++ )
				emojies.push ( <Emoji
					path={ `assets/faces/pack-${ pack }/${ i }.png` }
					key={ i }
					value={ i }
					hasPadding={ ! i }
					onChange={ onChange }
					isSelected={ face === i }
				/> );
			return emojies
		}) () }
	</div>
</HorzView>


const Emoji = ({ path, value, isSelected, hasPadding, onChange }) => <img
	src={ path }
	style={ styles.emoji ( isSelected, hasPadding ) }
	className="emoji transition-all"
	onClick={ () => onChange ( value ) }
	alt={ value }
/>



