import React from 'react'


const styles = {
	container : ( backgroundColor, size ) => ({
		backgroundColor,
		width : '800px', height : '800px',
		transform : `scale(${ size / 800 })`,
		margin : '-' + ( 800 - ( size / 800 ) / 2 ) + 'px'
	})
}


export default ({
	text,
	pack,
	face,
	imageStyle,
	textStyle,
	backgroundColor,
	size
}) => <div className="result-wrapper full-w flex flex-center">
	<div className="result flex-column flex-center transition-all" style={ styles.container ( backgroundColor, size ) }>
		{ face !== 0 && <div className="spacer"></div> }
		{ face !== 0 && <img style={ imageStyle } src={ `assets/faces/pack-${ pack }/${ face }.png` } alt={ face } /> }
		<div className="spacer"></div>
		<span style={ textStyle } className="transition-all">{ text }</span>
		{ text && <div className="spacer"></div> }
	</div>
</div>



